.page-header {
  margin-bottom: 42px;
}

.page-header__nav {
  position: relative;
}

.page-header__toggle {
  display: none;
  position: absolute;
  bottom: 18px;
  right: 20px;
  width: 23px;
  height: 21px;

  background-color: transparent;
  background-image: url("../img/icon-menu.svg");
  background-repeat: no-repeat;
  background-size: 23px 21px;
  border: none;
}

.page-header__bg {
  position: relative;
  height: 389px;

  background-color: #5b7683;
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto 100%;

  @media (min-resolution: 1.5dppx) {
    background-image: url("../img/bg-back-mobile@2x.jpg");
  }

  /* Текст на фоне на главной странице */

  &::before {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 280px;
    height: 332px;
    margin-top: -163px;
    margin-left: -140px;
    content: "";

    background-image: url("../img/text-sedona-index.svg");
    background-size: 280px 332px;
  }

  /* Фон на внутренних страницах */

  &--inner {
    height: 159px;

    background-position: bottom;
    background-size: auto;

    @media (min-resolution: 1.5dppx) {
      background-size: 768px auto;
    }
  }

  /* Текст на фоне на внутренних страницах */

  &--inner::before {
    width: 230px;
    height: 80px;
    margin-top: -47px;
    margin-left: -115px;

    background-image: url("../img/text-sedona.svg");
    background-size: 230px 80px;
  }

  /* Белая стрелка */

  &::after {
    position: absolute;
    left: 50%;
    bottom: -3px;
    width: 318px;
    height: 26px;
    margin-left: -160px;
    content: "";

    background-image: url("../img/bg-intro-triangle-mobile.svg");
    background-repeat: no-repeat;
    background-size: 320px 26px;
    background-position: bottom;

    border-left: solid #ffffff 1px;
    border-bottom: solid #ffffff 3px;
    border-right: solid #ffffff 1px;

    box-sizing: content-box;
  }
}

@media (min-width: 768px) {
  .page-header {
    margin-bottom: 50px;
  }

  .page-header__bg {
    height: 518px;

    background-image: url("../img/bg-back-tablet@1x.jpg");

    @media (min-resolution: 1.5dppx) {
      background-image: url("../img/bg-back-tablet@2x.jpg");
    }

    /* Текст на фоне на главной странице */

    &::before {
      width: 460px;
      height: 350px;
      margin-top: -182px;
      margin-left: -230px;

      background-size: 460px 350px;
    }

    /* Фон на внутренних страницах */

    &--inner {
      height: 291px;

      background-size: 100%;
    }

    /* Текст на фоне на внутренних страницах */

    &--inner::before {
      width: 456px;
      height: 80px;
      margin-top: -51px;
      margin-left: -227px;

      background-size: 456px 80px;
    }

    /* Белая стрелка */

    &::after {
      bottom: 0;
      width: 768px;
      height: 57px;
      margin-left: -384px;

      background-image: url("../img/bg-intro-triangle-tablet.svg");
      background-size: auto;

      border: none;
      box-sizing: inherit;
    }
  }
}

@media (min-width: 1200px) {
  .page-header__bg {
    background-image: url("../img/bg-back-desktop@1x.jpg");

    @media (min-resolution: 1.5dppx) {
      background-image: url("../img/bg-back-desktop@2x.jpg");
    }

    /* Текст на фоне на главной странице */

    &::before {
      margin-top: -180px;
    }

    /* Фон на внутренних страницах */

    &--inner {
      height: 301px;
    }

    /* Текст на фоне на внутренних страницах */

    &--inner::before {
      margin-top: -49px;
    }

    /* Белая стрелка */

    &::after {
      left: 0;
      width: 1200px;
      margin-left: 0;

      background-image: url("../img/bg-intro-triangle-desktop.svg");
    }
  }
}
